.btn {
    @include flexCenter;
    padding: 1.5rem;
    border-radius: 3px;
    cursor: pointer;
    transition: 0.4s;
    width: 20rem;

    &-white{
        margin: 0 5rem;
        color: $color-main-white;
        border: 3px solid $color-main-white;
        &:hover {
            background-color: rgba(255, 255, 255, 0.609);
            color: rgb(26, 26, 26);
            
        }
    }

    &-brown{
        font-size: 15rem;
        color: rgba(32, 21, 21, 0.863);
        // border: 3px solid $color-font-main;
        // background-color: rgba(255, 255, 255, 0.424);

        // &:hover {
        //     background-color: rgba(0, 0, 0, 0.609);
        //     color: $color-main-white;
            
        // }
    }
}
